body{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #333;
    color: aliceblue;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.coordForm{
    border-radius: 20px;
    margin-top: 50%;
    padding: 10%;
    width: 80%;
    background: #242424;
    -webkit-box-shadow: 0px 8px 14px 1px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0px 8px 14px 1px rgba(0, 0, 0, 0.3);
    box-shadow: 0px 8px 14px 1px rgba(0, 0, 0, 0.3);
}

.coordForm_form{
    margin: 0 10%;
    width: 80%;
}


.coordForm h1{
    text-align: center;
    color: aqua;
    font-size: 25px;
}
.coordForm_TextPlaces{
    width: 100%;
    margin: 0 5%;
}
.coordForm_TextPlaces input{
    padding: 2%;
    margin: 2%;
    background: #fff;
    border-radius: 10px;
}

.inp{
    padding: 10px;
    border-radius: 20px;
    background: #fff;
}

.coordForm_btn{
    width: 100%;
    margin: 3% 38%;
}

.coordForm_btn input{
    font-weight: bold;
    padding: 1% 2% 1% 2%;
    border-radius: 10px;
    background: #f6aa12;
}