.help{
    position: absolute;
    top: 20px;
    right: 60px;
    background: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    transition: all 0.5s ease;
    cursor: pointer;
    overflow: hidden;
}

.help:is(:not(:hover)){
    animation: shadowPulse 2s infinite;
}

.help:hover{
    width: 350px;
    height: 150px;
    border-radius: 10px;
}

.help:hover .message{
    opacity: 1;
    transition: all 0.5s 0.5s ease;
}

.help:hover .question-mark{
    display: none;
}

.message{
    opacity: 0;
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.message-title{
    font-size: 1.5em;
    margin-bottom: 10px;
    font-weight: bold;
    color: #333;
}

.message-text{
    font-size: 1em;
    color: #333;

}
.question-mark{
    position: absolute;
    margin-left: 37%;
    font-size: 1.5em;
    color: #333;
}
@keyframes shadowPulse{
    0% {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }

    50% {
        box-shadow: 0 0 15px rgba(51, 70, 244, 0.868);
    }

    100% {
        box-shadow:  0 0 10px rgba(0, 0, 0, 0.1);
    }

}

a{
    color: #c58a00;
    text-decoration: none;
    font-weight: bold;
}